import React, { useState } from 'react';
import { TextField, Radio, RadioGroup, FormControlLabel, Button, Typography } from "@mui/material";
import RadioButtonUncheckedIcon from '@mui/icons-material/RadioButtonUnchecked';
import RadioButtonCheckedIcon from '@mui/icons-material/RadioButtonChecked';
import { useNavigate } from 'react-router-dom';

const MatchTime = () => {
    const [team1, setTeam1] = useState('');
    const [team2, setTeam2] = useState('');
    const [datetime, setDatetime] = useState('');
    const [status, setStatus] = useState('');
    const navigate = useNavigate();

    const handleStatusChange = (e) => {
        setStatus(e.target.value);
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        
        const formData = {
            team1: team1,
            team2: team2,
            date: datetime,
            status: status
        };

        fetch('https://mytestjson1.s3.eu-north-1.amazonaws.com/1round.json')
            .then(response => response.json())
            .then(data => {
                console.log('Current data:', data);

                // Проверяем, является ли data.team массивом
                if (!Array.isArray(data.team)) {
                    data.team = [];
                }

                // Добавляем новую команду в массив
                data.team.push(formData);
                console.log('Updated data:', data);

                // Отправляем запрос на обновление данных
                fetch('https://mytestjson1.s3.eu-north-1.amazonaws.com/1round.json', {
                    method: 'PUT',
                    headers: {
                        'Content-Type': 'application/json'
                    },
                    body: JSON.stringify(data)
                })
                .then(response => {
                    if (response.ok) {
                        console.log('Data successfully added');
                        navigate('/tournaments')
                    }
                })
                .catch(error => {
                    console.error('Error sending request:', error);
                });
            })
            .catch(error => {
                console.error('Error loading data:', error);
            });
    };



    

    return (
        <form noValidate autoComplete="off" onSubmit={handleSubmit} style={{border: "2px solid black", padding: "10px"}}>
            <Typography variant="h3" style={{marginBottom: "20px", textAlign: "center"}}>Додавання розкладу матчів</Typography>
            <TextField
                onChange={(e) => setTeam1(e.target.value)}
                variant="outlined"
                label="Team 1"
                required
                fullWidth
            />
            <br /><br />
            <TextField
                onChange={(e) => setTeam2(e.target.value)}
                variant="outlined"
                label="Team 2"
                required
                fullWidth
            />
            <br /><br />
            <TextField
                onChange={(e) => setDatetime(e.target.value)}
                variant="outlined"
                label="16.01 15:00"
                required
                fullWidth
            />
            <br /><br />
            <RadioGroup aria-label="status" name="status" value={status} onChange={handleStatusChange}>
                <FormControlLabel
                    value="pending"
                    control={<Radio icon={<RadioButtonUncheckedIcon />} checkedIcon={<RadioButtonCheckedIcon />} />}
                    label="Очікування"
                />
                <FormControlLabel
                    value="in_progress"
                    control={<Radio icon={<RadioButtonUncheckedIcon />} checkedIcon={<RadioButtonCheckedIcon />} />}
                    label="У процессі"
                />
                <FormControlLabel
                    value="completed"
                    control={<Radio icon={<RadioButtonUncheckedIcon />} checkedIcon={<RadioButtonCheckedIcon />} />}
                    label="Закінчено"
                />
            </RadioGroup>
            <Button
            variant="contained"
            type="submit"
            style={{marginBottom: "100px"}}

            >
            Готово
            </Button>
        </form>
    );
};

export default MatchTime;