import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { Button, Typography } from '@mui/material';
import { useEffect, useState } from 'react';

export function Round3() {
    const [show, setShow] = useState(false);
    const [showAll, setShowAll] = useState(false);
    const [matchtime, setMatchtime] = useState([]);

    const handleShowAll = () => {
        setShowAll(!showAll);
    }

    const handleShow = () => {
        setShow(!show);
    }

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://mytestjson1.s3.eu-north-1.amazonaws.com/3round.json');
                const data = await response.json();
                setMatchtime(data.team.reverse());
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, []);

    return (
        <div style={{ width: "100%", display: "flex", flexDirection: "column", alignItems: "center" }}>
            {!show && (
                <Button
                    variant="outlined"
                    color="primary"
                    type="button"
                    onClick={handleShow}
                    style={{ width: "70%", height: "50px", background: "#FEFF51" }}
                >
                    <span style={{ color: "black" }}>Третій тур</span>
                    <ExpandMoreIcon fontSize="large" style={{ position: "absolute", right: "20px" }} />
                </Button>
            )}
            {show && (
                <div style={{ width: "100%", display: "flex", flexDirection: "column", alignItems: "center" }}>
                    <Button
                        variant="outlined"
                        color="primary"
                        type="button"
                        onClick={handleShow}
                        style={{ width: "70%", height: "50px", background: "#FEFF51" }}
                    >
                        <span style={{ color: "black" }}>Третій тур</span>
                        <ExpandLessIcon fontSize="large" style={{ position: "absolute", right: "20px" }} />
                    </Button>

                    {matchtime.length === 0 ? (
                        <Typography variant="body1" color="secondary" style={{ marginTop: '20px', fontSize: "16px" }}>Поки що жодного матчу. Чекайте</Typography>
                    ) : (
                        <div style={{ width: "100%", display: "flex", flexDirection: "column", gap: "20px", alignItems: "center" }}>
                            {matchtime.slice(0, 3).map((match, index) => (
                                <div className="match-info" key={index} >
                                    <div className="team">
                                        <span className="team-name">{match.team1}</span>
                                        <span className="versus">vs</span>
                                        <span className="team-name">{match.team2}</span>
                                    </div>
                                    <div className="datetime">
                                        <span className="date">{match.date}</span>
                                    </div>
                                    <div className="status">
                                        {match.status === "in_progress" && (
                                            <span>В процесі</span>
                                        )}
                                        {match.status === "pending" && (
                                            <span>Очікування</span>
                                        )}
                                        {match.status === "completed" && (
                                            <span>Закінчено</span>
                                        )}
                                        <div>
                                            {match.status === "in_progress" && (
                                                <div className="in_progress"></div>
                                            )}
                                            {match.status === "pending" && (
                                                <div className="pending"></div>
                                            )}
                                            {match.status === "completed" && (
                                                <div className="completed"></div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}

                    {!showAll && matchtime.length > 3 && (
                        <Button
                            variant="outlined"
                            color="secondary"
                            type="button"
                            endIcon={<ExpandMoreIcon fontSize="large" />}
                            onClick={handleShowAll}
                            style={{ marginTop: "20px" }}
                        >
                            Показати більше
                        </Button>
                    )}
                    {showAll && (
                        <div style={{ width: "100%", display: "flex", flexDirection: "column", gap: "20px", alignItems: "center", marginTop: "20px" }}>
                            {matchtime.slice(3).map((match, index) => (
                                <div className="match-info" key={index + 3}>
                                    <div className="team">
                                        <span className="team-name">{match.team1}</span>
                                        <span className="versus">vs</span>
                                        <span className="team-name">{match.team2}</span>
                                    </div>
                                    <div className="datetime">
                                        <span className="date">{match.date}</span>
                                    </div>
                                    <div className="status">
                                        <span>{match.status}</span>
                                        <div>
                                            {match.status === "in_progress" && (
                                                <div className="in_progress"></div>
                                            )}
                                            {match.status === "pending" && (
                                                <div className="pending"></div>
                                            )}
                                            {match.status === "completed" && (
                                                <div className="completed"></div>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            ))}
                            <Button
                                variant="outlined"
                                type="button"
                                color="secondary"
                                endIcon={<ExpandLessIcon fontSize="large" />}
                                onClick={handleShowAll}
                                style={{}}
                            >
                                Сховати
                            </Button>
                        </div>
                    )}
                </div>
            )}
        </div>
    )
}
