import React, { useEffect, useRef, useState } from "react";
import { makeStyles } from "@mui/styles";
import { Drawer, List, ListItem, ListItemIcon, ListItemText, Typography, AppBar, Toolbar, IconButton, useMediaQuery, ThemeProvider, createTheme } from "@mui/material";
import { SubjectOutlined, AddCircleOutlineOutlined } from "@mui/icons-material";
import MenuIcon from '@mui/icons-material/Menu';
import Header from "../Header/Header";
import AnnouncementIcon from '@mui/icons-material/Announcement';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';
import ArticleIcon from '@mui/icons-material/Article';
import ChecklistIcon from '@mui/icons-material/Checklist';
import InfoIcon from '@mui/icons-material/Info';
import { useLocation, useNavigate } from "react-router-dom";

var drawerWidth = 240;
const useStyles = makeStyles((theme) => {
        return {
            page: {
                background: '#f9f9f9',
                width: '100vw',
                flexGrow: 1,
                padding: theme.spacing(3),
                transition: 'margin-left 0.2s ease',
                marginLeft: -drawerWidth,
            },
            pageShift: {
                width: '100vw',
                background: '#f9f9f9',
                padding: theme.spacing(3),
                marginLeft: 0,
                transition: 'margin-left 0.2s ease',
            },
            page_zeropadding: {
                background: '#f9f9f9',
                width: '100vw',
                flexGrow: 1,
                transition: 'margin-left 0.2s ease',
                marginLeft: -drawerWidth
            },
            pageShift_zeropadding: {
                width: '100vw',
                background: '#f9f9f9',
                marginLeft: 0,
                transition: 'margin-left 0.2s ease',
            },
            appBar: {
                background: "#1C1C84",
                transition: 'margin-left 0.2s ease',
                marginLeft: drawerWidth
            },
            
            appBarShift: {
                background: "#1C1C84",
                marginLeft: 0,
                transition: 'margin-left 0.2s ease',
                width: '97%',
            },
            drawer: {
                width: drawerWidth,
            },
            drawerPaper: {
                width: drawerWidth,
            },
            drawerPaperBlack:{
                width: drawerWidth,
            },
            root: {
                display: 'flex',
            },
            toolbar: theme.mixins.toolbar,
            title: {
                flexGrow: 1,
            },
            icon: {
                marginLeft: "50%"
            },
            '@media screen and (max-width: 1150px)': {
                pageShift: {
                    marginLeft: -drawerWidth,
                },
                pageShift_zeropadding: {
                    marginLeft: -drawerWidth,
                },
                page: {
                    padding: theme.spacing(0)
    
                },
            }
        };
    }
);


    

const Layout = ({ children, themeMode, toggleTheme }) => {
    const classes = useStyles();
    const navigate = useNavigate();
    const location = useLocation();
    const [isTournamentsPage, setIsTournamentsPage] = useState(false);
    const [isTournamentsGridPage, setIsTournamentsGridPage] = useState(false);
    const [isCsgoPage, setIsCsgoPage] = useState(false);
    const [isDrawerOpen, setIsDrawerOpen] = useState(false);
    const drawerRef = useRef(null); // Добавляем ref для Drawer
    const toggleDrawer = () => {
        setIsDrawerOpen(!isDrawerOpen);
    };

    useEffect(() => {
        setIsTournamentsPage(location.pathname === '/tournaments');
    }, [location.pathname]);
    useEffect(() => {
        setIsTournamentsGridPage(location.pathname === '/tournamentsgrid');
    }, [location.pathname]);
    useEffect(() => {
        setIsCsgoPage(location.pathname === '/csgo');
    }, [location.pathname]);

    const lightTheme = createTheme({
        palette: {
          mode: 'light',
          primary: {
            main: '#333'
          },
          secondary: {
            main: '#fff'
          },
        }
      });

    //пути, для которых вы хотите установить padding в 0
    const zeroPaddingPaths = ['/csgo', '/tournamentsgrid'];
    const shouldSetZeroPadding = zeroPaddingPaths.includes(location.pathname);
      
      const darkTheme = createTheme({
        palette: {
          mode: 'dark',
          primary: {
            main: '#333'
          },
          secondary: {
            main: '#fff'
          },
        }
      });

    const menu = [
        {
            text: "Головна",
            icon: <SubjectOutlined color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/"
        },
        {
            text: "Петиції",
            icon: <ArticleIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/notes"
        },
        {
            text: "Новини",
            icon: <AnnouncementIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/news"
        },
        {
            text: "Турніри",
            icon: <EmojiEventsIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/tournaments"
        }
    ];

    const mobileMenu = [
        {
            text: "Головна",
            icon: <SubjectOutlined color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/"
        },
        {
            text: "Петиції",
            icon: <ArticleIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/notes"
        },
        {
            text: "Новини",
            icon: <AnnouncementIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/news"
        },
        {
            text: "Про нас",
            icon: <InfoIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/about"
        },
        {
            text: "Наші цілі",
            icon: <ChecklistIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/goals"
        },
        {
            text: "Турніри",
            icon: <EmojiEventsIcon color={isTournamentsPage || isTournamentsGridPage ? "secondary" : "primary"} />,
            path: "/tournaments"
        }
    ];

    const isMobile = useMediaQuery('(max-width:1150px)'); // Пример медиа-запроса

    const menuItems = isMobile ? mobileMenu : menu;

    const handleBackgroundClick = () => {
        if (isDrawerOpen) {
          setIsDrawerOpen(false);
        }
      };

    return (
        <ThemeProvider theme={isTournamentsPage || isTournamentsGridPage || isCsgoPage ? darkTheme : lightTheme}>
        <div className={classes.root}>
            <AppBar>
                <Toolbar className={isDrawerOpen ? classes.appBar : classes.appBarShift}>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        edge="start"
                        onClick={toggleDrawer}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>KnappTeam</Typography>
                    <Header />
                </Toolbar>
            </AppBar>

            <Drawer
                className={classes.drawer}
                variant="persistent"
                anchor={isMobile ? "top" : "left"} // Изменение анкора в зависимости от мобильного устройства
                open={isDrawerOpen}
                classes={{
                    paper: isMobile ? classes.drawerPaperMobile : classes.drawerPaper,
                }}
            >
                <Typography variant="h4" color={isTournamentsPage || isTournamentsGridPage || isCsgoPage ? "secondary" : "primary"}>KnappT</Typography >
                <List>
                    {menuItems.map((item) => (
                        <ListItem button key={item.text} onClick={() => {
                            navigate(item.path); // Переход по ссылке
                            toggleDrawer(); // Закрытие Drawer на мобильных устройствах
                        }}>
                            <ListItemIcon>{item.icon}</ListItemIcon>
                            <ListItemText primary={item.text} />
                        </ListItem>
                    ))}
                </List>
            </Drawer>

            <div
      className={isDrawerOpen ? (shouldSetZeroPadding ? classes.pageShift_zeropadding : classes.pageShift) : (shouldSetZeroPadding ? classes.page_zeropadding : classes.page)}
      style={{ background: isTournamentsPage || isTournamentsGridPage || isCsgoPage ? '#1F0C19' : '#f9f9f9' }}
      onClick={handleBackgroundClick}
    >
      <div className={classes.toolbar} />
      {children}
    </div>

            </div>
        </ThemeProvider>
    );
};

export default Layout;