import { Button, Dialog, DialogActions, DialogTitle, TextField, Typography } from "@mui/material";
import { useState } from "react";
import { useNavigate } from "react-router-dom";
import MatchTime from "../MatchTime/MatchTime"; 

export function Group5(props) {
    const [team1_1_1_1_1_1, setTeam1_1_1_1_1_1] = useState('');
    const [team2_2_2_2_2_2, setTeam2_2_2_2_2_2] = useState('');
    const [team3_3_3_3_3_3, setTeam3_3_3_3_3_3] = useState('');

    const navigate = useNavigate();

    const handleSubmit = (e) => {
        e.preventDefault();

        const teamsObject = {
            team1_1_1_1_1_1, team2_2_2_2_2_2, team3_3_3_3_3_3
        };

        for (let key in teamsObject) {
            if (teamsObject[key].trim() === '') {
                delete teamsObject[key];
            }
        }

        const hasTeam = Object.keys(teamsObject).length > 0;
        if (hasTeam) {
            console.log('Teams object:', teamsObject);

            fetch('https://mytestjson1.s3.eu-north-1.amazonaws.com/Grid_teams.json')
                .then(response => response.json())
                .then(data => {
                    console.log('Current data:', data);

                    if (typeof data.teams5 !== 'object' || Array.isArray(data.teams5)) {
                        data.teams1 = {};
                    }

                    Object.assign(data.teams5, teamsObject);
                    console.log('Updated data:', data);

                    fetch('https://mytestjson1.s3.eu-north-1.amazonaws.com/Grid_teams.json', {
                        method: 'PUT',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(data)
                    })
                    .then(response => {
                        if (response.ok) {
                            console.log('Data successfully added');
                            navigate('/tournaments')
                        }
                    })
                    .catch(error => {
                        console.error('Error sending request:', error);
                    });
                })
                .catch(error => {
                    console.error('Error loading data:', error);
                });
        }
    };

    return (
        <div style={{display: "flex", flexDirection: "column", gap: "50px", width: "100%"}}>
            <Typography variant="h3" color="primary" style={{textAlign:"center"}}> Команди, що пройшли у фінал</Typography>
            <form noValidate autoComplete="off" onSubmit={handleSubmit} style={{width: "100%", display: "flex", flexDirection: "column", border: "2px solid black"}} >
            <Typography variant="h4" color="primary" style={{textAlign:"center"}}>2 фінальні команди</Typography>
            <div style={{display: "flex", justifyContent: "space-around"}}>
                <div style={{width: "20%", height: "100%", display: "flex", flexDirection: "column", justifyContent: "space-around"}}>
                    <TextField
                        onChange={(e) => setTeam1_1_1_1_1_1(e.target.value)}
                        variant="outlined"
                        label="Team 1"
                        required
                        fullWidth
                    />
                    <br /><br />
                    <TextField
                        onChange={(e) => setTeam2_2_2_2_2_2(e.target.value)}
                        variant="outlined"
                        label="Team 2"
                        required
                        fullWidth
                    />
                </div>
            </div>
            <Typography variant="h4" color="primary" style={{textAlign:"center"}}>Переможешь</Typography>
            <div style={{display: "flex", justifyContent: "space-around"}}>
                <div style={{width: "20%", height: "100%", display: "flex", flexDirection: "column", justifyContent: "space-around"}}>
                    <TextField
                        onChange={(e) => setTeam3_3_3_3_3_3(e.target.value)}
                        variant="outlined"
                        label="Team 3"
                        required
                        fullWidth
                    />
                </div>
            </div>
            <Button
                variant="contained"
                type="submit"
                style={{marginBottom: "50px",marginTop: "50px", width: "30%", marginLeft: "auto", marginRight: "auto" }}
            >
                Готово
            </Button>
        </form>
        </div>
        
    );

}